"use client";

import { useEffect, useState } from "react";
import { createClient } from "@supabase/supabase-js";

const supabase = createClient(
  process.env.NEXT_PUBLIC_SUPABASE_URL!,
  process.env.NEXT_PUBLIC_SUPABASE_KEY!
);

export default function Page() {
  const [intelFeed, setIntelFeed] = useState([]);
  const [newIntel, setNewIntel] = useState({
    headline: "",
    summary: "",
    region: "",
    sentiment: "Neutral",
    impact: 50,
  });
  const [activeCategory, setActiveCategory] = useState("All");

  useEffect(() => {
    fetchIntel();
    const channel = supabase
      .channel("intel_feed")
      .on(
        "postgres_changes",
        { event: "INSERT", schema: "public", table: "intel_feed" },
        (payload) => {
          setIntelFeed((prev) => [payload.new, ...prev]);
        }
      )
      .subscribe();
    return () => supabase.removeChannel(channel);
  }, []);

  async function fetchIntel() {
    const { data } = await supabase
      .from("intel_feed")
      .select("*")
      .order("created_at", { ascending: false });
    if (data) setIntelFeed(data);
  }

  async function submitIntel(e) {
    e.preventDefault();
    await supabase.from("intel_feed").insert([newIntel]);
    setNewIntel({
      headline: "",
      summary: "",
      region: "",
      sentiment: "Neutral",
      impact: 50,
    });
  }

  return (
    <main className="min-h-screen bg-gradient-to-b from-slate-900 to-slate-800 text-white p-8 flex flex-col">
      <h1 className="text-2xl font-semibold text-sky-400 mb-4">
        eX Intelligence Command Center üíô
      </h1>
      <p className="text-sm text-green-400 mb-6">‚úÖ Live connection established</p>

      {/* Briefing Categories Bar */}
      <div className="flex gap-4 border-b border-slate-700 pb-2 mb-6 text-sm">
        {["All", "Global Politics", "Finance", "Outbreaks", "Conflicts", "Tech", "Environment"].map(
          (cat) => (
            <button
              key={cat}
              onClick={() => setActiveCategory(cat)}
              className={`transition-colors ${
                activeCategory === cat
                  ? "text-sky-300 border-b border-sky-400 pb-1"
                  : "hover:text-sky-300 text-slate-300"
              }`}
            >
              {cat}
            </button>
          )
        )}
      </div>

      <div className="flex gap-8">
        {/* Intel Feed */}
        <div className="flex-1 space-y-4">
          {intelFeed.map((intel) => (
            <div
              key={intel.id}
              className="bg-slate-800 rounded-xl p-4 border border-slate-700"
            >
              <h2 className="text-sky-300 font-semibold">‚Äú{intel.headline}‚Äù</h2>
              <p className="text-sm text-slate-300">{intel.summary}</p>
              <div className="text-xs text-slate-400 mt-2 flex justify-between">
                <span>üåç {intel.region}</span>
                <span>
                  ‚ö° Impact: {intel.impact} | {intel.sentiment}
                </span>
              </div>
            </div>
          ))}
        </div>

        {/* Right-Side Globe + Input */}
        <div className="w-[340px] flex flex-col items-center">
          {/* Globe Placeholder */}
          <div className="w-52 h-52 bg-gradient-to-b from-slate-700 to-black rounded-full shadow-inner shadow-blue-500 mb-4" />

          {/* Sentiment Counts */}
          <div className="flex justify-center gap-4 text-xs mb-4">
            <span className="text-yellow-400">‚óè Positive: 0</span>
            <span className="text-gray-400">‚óè Neutral: 0</span>
            <span className="text-red-400">‚óè Negative: 0</span>
            <span className="text-slate-500">‚óè Unknown: 1</span>
          </div>

          {/* Add New Intel */}
          <form
            onSubmit={submitIntel}
            className="bg-slate-800 p-4 rounded-xl w-full border border-slate-700"
          >
            <h3 className="text-pink-400 font-semibold mb-2">üß† Add New Intel</h3>
            <input
              type="text"
              placeholder="Headline"
              value={newIntel.headline}
              onChange={(e) =>
                setNewIntel({ ...newIntel, headline: e.target.value })
              }
              className="w-full p-2 mb-2 rounded bg-slate-900 text-white text-sm"
            />
            <textarea
              placeholder="Summary"
              value={newIntel.summary}
              onChange={(e) =>
                setNewIntel({ ...newIntel, summary: e.target.value })
              }
              className="w-full p-2 mb-2 rounded bg-slate-900 text-white text-sm"
            />
            <input
              type="text"
              placeholder="Region (e.g., Canada)"
              value={newIntel.region}
              onChange={(e) =>
                setNewIntel({ ...newIntel, region: e.target.value })
              }
              className="w-full p-2 mb-2 rounded bg-slate-900 text-white text-sm"
            />
            <div className="flex gap-2 mb-2">
              <select
                value={newIntel.sentiment}
                onChange={(e) =>
                  setNewIntel({ ...newIntel, sentiment: e.target.value })
                }
                className="flex-1 p-2 rounded bg-slate-900 text-white text-sm"
              >
                <option>Positive</option>
                <option>Neutral</option>
                <option>Negative</option>
                <option>Unknown</option>
              </select>
              <input
                type="number"
                value={newIntel.impact}
                onChange={(e) =>
                  setNewIntel({ ...newIntel, impact: Number(e.target.value) })
                }
                className="w-20 p-2 rounded bg-slate-900 text-white text-sm"
              />
            </div>
            <button
              type="submit"
              className="w-full bg-blue-600 hover:bg-blue-500 text-white rounded p-2 text-sm"
            >
              üöÄ Submit Intel
            </button>
          </form>

          {/* Country Panel */}
          <div className="mt-6 w-full text-sm text-slate-300 border-t border-slate-700 pt-3">
            <h4 className="text-sky-400 font-semibold mb-2">
              üåç Country Intelligence
            </h4>
            <ul className="space-y-1">
              {[
                "United States",
                "United Kingdom",
                "Germany",
                "Japan",
                "Canada",
                "Nigeria",
                "China",
                "India",
              ].map((country) => (
                <li key={country} className="hover:text-sky-300 cursor-pointer">
                  {country}
                </li>
              ))}
            </ul>
          </div>

          {/* Predictive Timeline Mock */}
          <div className="mt-6 w-full bg-slate-900 rounded-lg p-3 text-xs text-center text-slate-400">
            ‚ñë‚ñí‚ñí‚ñì‚ñì‚ñì‚ñì‚ñí‚ñí‚ñë‚ñë ‚Äî{" "}
            <span className="text-sky-400">Predictive Timeline:</span> Today ‚Üí +90 Days
          </div>

          {/* Finance Overview - Visible only when Finance is active */}
          {activeCategory === "Finance" && (
            <div className="mt-6 w-full bg-slate-800 border border-sky-700 rounded-xl p-4 animate-fadeIn">
              <h4 className="text-sky-300 font-semibold mb-3 text-center">
                üíπ Finance Overview
              </h4>
              <div className="grid grid-cols-2 gap-3 text-sm">
                {/* Legacy Assets */}
                <div className="bg-slate-900 p-3 rounded-lg border border-yellow-600 shadow-inner">
                  <h5 className="text-yellow-400 mb-2 font-medium">
                    Legacy Assets
                  </h5>
                  <p>ü™ô Gold ‚Äî $2,385 <span className="text-green-400">(+0.3%)</span></p>
                  <p>üìà S&amp;P 500 ‚Äî 5,165 <span className="text-red-400">(‚àí0.4%)</span></p>
                  <p>üõ¢Ô∏è Oil ‚Äî $82.11 <span className="text-green-400">(+1.1%)</span></p>
                </div>

                {/* Emerging Assets */}
                <div className="bg-slate-900 p-3 rounded-lg border border-sky-600 shadow-inner">
                  <h5 className="text-sky-400 mb-2 font-medium">
                    Emerging Assets
                  </h5>
                  <p>‚Çø Bitcoin ‚Äî $68,420 <span className="text-green-400">(+0.9%)</span></p>
                  <p>ü™ô Ethereum ‚Äî $2,530 <span className="text-red-400">(‚àí0.7%)</span></p>
                  <p>üåê Solana ‚Äî $162 <span className="text-green-400">(+2.4%)</span></p>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </main>
  );
}